html, body, #root {
  margin: 0;
  height: 100%;
  font-family: 'Open Sans', sans-serif;
}

body, h1, h2, h3 {
  font-weight: 300;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: #5aadbb;
}

.navbar-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 20;
}

.navbar .navbar-nav {
  display: inline-block;
  float: none;
  vertical-align: top;
}

.navbar .navbar-collapse {
  text-align: center;
}

.services-wrapper, .wrapper-clone {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-image: url("images/yoga.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: relative;
}

.wrapper-clone {
  z-index: 9;
  position: absolute;
  background-image: none;
}

.no-border {
  border: none;
}

.services-panel {
  height: 75%;
  width: 800px;
  opacity: 0.75;
  background-color: #ecfafb;
}

.services-btn-group {
  margin-left: 50px;
  height: 500px;
  width: 700px;
  z-index: 10;
  top: 30px;
}

.services-text {
  height: 70px;
  width: 170px;
  font-weight: bold;
  display: table-cell;
  vertical-align: middle;
}

.services-btn {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  margin: 30px 30px 0px 30px;
  white-space: normal;
  color: #3d5d61;
  background-image: -webkit-linear-gradient(top, #fff 30%, #a2bbbd 100%);
  background-image: linear-gradient(to bottom, #fff 30%, #a2bbbd 100%);
  border: 1px;
}

.services-btn:hover {
  background-color: #fff;
  background-image: -webkit-linear-gradient(top, #a2bbbd 20%, #fff 100%);
  background-image: linear-gradient(to bottom, #a2bbbd 20%, #fff 100%);
}

.services-btn:hover .services-text {
  font-size: 120%;
}

.about-wrapper {
  padding: 8% 20% 5% 10%;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #417c82;
}

.about-header {
  text-align: center;
}

.about-header .heading {
  font-size: 50px;
  line-height: 45px;
  letter-spacing: 1px;
  color: #fefefe;
  font-family: 'Mada', sans-serif;
}

.about-header .heading-bottom {
  margin-bottom: 40px;
  color: #1d5871;
  text-transform: uppercase;
  font-size: 22px;
}

.about-desc {
  margin-left: 15px;
  color: #c8dbe1;
  font-size: 20px;
}

.rutu-pic {
  float: left;
}

.carousel-inner > .item > img {
  width: 100%;
  height: 750px;
}

.cover-base {
  height: 200px;
  background-color: #406067;
}

.contact-wrapper {
  height: 100%;
  width: 100%;
}

/* RESPONSIVE CSS */
@media (min-width: 768px) {
  /* Navbar positioning foo */
  .navbar-wrapper {
    margin-top: 20px;
  }
  .navbar-wrapper .container {
    padding-right: 15px;
    padding-left: 15px;
  }
  .navbar-wrapper .navbar {
    padding-right: 0;
    padding-left: 0;
  }
  /* The navbar becomes detached from the top, so we round the corners */
  .navbar-wrapper .navbar {
    border-radius: 4px;
  }
  /* Bump up size of carousel content */
  .carousel-caption p {
    margin-bottom: 20px;
    font-size: 21px;
    line-height: 1.4;
  }
  .featurette-heading {
    font-size: 50px;
  }
}

